// http://tjvantoll.com/2013/04/15/list-of-pseudo-elements-to-style-form-controls/

fieldset {
	border: none;
	padding: 0;
	margin: 0;
}


select,
input:not([type="checkbox"]):not([type="radio"]),
button {

	--input-size: 32px;
	&[icon]:empty {
		--input-size: 40px;
	}
	
	&[dense][icon]:empty,
	&[small][icon]:empty,
	&[dense],
	&[small] {
		--input-size: 26px;
	}
	&[big],
	&[spacious] {
		--input-size: 40px;
	}

	// TODO: it'd be nice to have another size of 48px

	&[small] {
		// custom small icon size
		--size: 16px;
		--font-size: 14px;
	}
	&[big] {
		//--size: 16px;
		//--font-size: 14px;
	}

	--seam-size: 1px;
	&[seam] {
		border: var(--seam-size) solid var(--seam-color);
	}

}

